<template>
  <div class="InBoxBody">
    <div class="InBoxBody-header">
      <span class="InBoxBody-header-text">视频课程</span>
      <el-button class="buttonRight" round @click="handleEdit('1')">新 增</el-button>
    </div>
    <div class="InBoxBody-con">
      <div class="InBoxBody-con-input">
        <span>信息搜索</span>
        <el-input class="input" v-model="form.name" placeholder="视频课程名称"></el-input>
        <el-select class="input" v-model="form.mem_id" clearable placeholder="请选择机构">
          <el-option v-for="item in $storeState.JGgllist" :key="item.mem_id" :label="item.cname" :value="item.mem_id">
          </el-option>
        </el-select>
        <el-button class="time" type="warning" plain @click="scIN()">搜 索</el-button>
      </div>
      <!-- 表单 -->
      <el-table ref="multipleTable" :data="tableData" style="width: 100%" stripe tooltip-effect="dark"
        :header-cell-style="{background:'#eef1f6',color:'#606266'}">
				<el-table-column prop="cname" label="所属机构" align="center"></el-table-column>
        <el-table-column prop="sort" label="排序" align="center"></el-table-column>
        <el-table-column prop="c_num" label="课时" align="center"></el-table-column>
				<el-table-column prop="password" label="视频密码" align="center"></el-table-column>
        <el-table-column prop="name" label="课程名称" align="center"></el-table-column>
        <el-table-column label="操作" width="360" align="center">
          <template slot-scope="scope">
            <el-button size="mini" type="warning" @click="CopyKC(scope.row)">课程复用</el-button>
            <el-button size="mini" type="warning" @click="handleEdit('2',scope.$index, scope.row)">编辑</el-button>
            <el-button size="mini" type="success" @click="toClass(scope.$index, scope.row)">视频列表</el-button>
            <el-button size="mini" @click="handleOK(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="block pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="form.page"
          :page-size="16" layout="prev, pager, next, jumper" :total="num">
        </el-pagination>
      </div>
    </div>
    <!-- 弹出窗 -->
    <el-dialog title="视频课程" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" label-position="top">
        <el-form-item label="视频课程名称" prop="name">
          <el-input size="large" v-model="ruleForm.name" placeholder="清输入视频课程名称"></el-input>
        </el-form-item>
        <el-form-item label="视频课程排序" prop="sort">
          <el-input size="large" type="number" v-model="ruleForm.sort" placeholder="请输入排序如:99"></el-input>
        </el-form-item>
				<el-form-item label="视频课程密码">
				  <el-input size="large" v-model="ruleForm.password" placeholder="设置视频课程密码"></el-input>
				</el-form-item>
        <el-form-item class="inupdbox" label="课目分类" prop="valkm">
        	<el-cascader
        	    size="large"
        		style="width: 100%;"
        	    v-model="ruleForm.valkm"
        	    :options="optionskm"
        	    :props="{ expandTrigger: 'hover' }">
        	</el-cascader>
        </el-form-item>
        <el-form-item label="封面单图(上传jpg/png文件，尺寸400*400,大小不超过2M):" prop="cover_img">
          <el-upload action="https://zht.insooner.com/jg/v1/getUpdatePictures" list-type="picture-card" :file-list="ruleForm.cover_img"
            :on-preview="handlePictureCardPreview" :on-success="handsuccess" :on-remove="handleRemove" :limit="1">
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisibleimg">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">确 认</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 复用弹窗 -->
    <!-- 复用对话框 -->
    <el-dialog title="机构课程复用" top="30vh" width="30%" :visible.sync="CopyKCidVisible">
      <el-select v-model="CopyKCidform.mem_id" style="width: 100%;" size="large" multiple placeholder="请选择要复用的机构">
        <el-option v-for="item in $storeState.JGgllist" :key="item.mem_id" :label="item.cname" :value="item.mem_id">
        </el-option>
      </el-select>
      <div slot="footer" class="dialog-footer">
        <el-button @click="CopyKCidVisible = false">取 消</el-button>
        <el-button type="primary" @click="CopyKCcli()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        //总页数
        num: 1,
        //获取的分类id
        classid: null,
        //分类
        optionskm:[],
        //提交内容
        ruleForm: {
          name: '',
					password:'',
          valkm:'',
          cover_img: [],
          sort: ''
        },
        rules: {
          name: [{
            required: true,
            message: '请输入课程名称',
            trigger: 'blur'
          }],
			valkm: [{
			  required: true,
			  message: '请选择课程分类',
			  trigger: 'blur'
			}],
          cover_img: [{
            required: true,
            message: '请上传视频课程图片',
            trigger: 'blur'
          }],
          sort: [{
            required: true,
            message: '请填写课程排序',
            trigger: 'blur'
          }]
        },
        dialogVisible: false,
        dialogVisibleimg: false,
        dialogImageUrl: '',
        //搜索内容
        form: {
          name: '',
		  mem_id:'',
          page: 1
        },
        tableData: [],
        //复用
        CopyKCidVisible: false,
        CopyKCidform: {
          mem_id: '',
          course_id: ''
        }
      }
    },
    methods: {
      //图片上传
      handleRemove(file, fileList) {
        this.ruleForm.cover_img = fileList;
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      // 上传图片成功
      handsuccess(file, fileList, fileimg) {
        this.ruleForm.cover_img = fileimg;
      },
      //确认
      CopyKCcli() {
        let that = this;
        that.$post('v1/copyjgCourseVideo', that.CopyKCidform).then((response) => {
          if (response.code == 1000) {
            that.CopyKCidVisible = false;
            //机构列表渲染
            this.ObjAjax();
          };
        });
      },
      //课程复制
      CopyKC(item) {
        this.CopyKCidform.course_id = item.id;
        this.CopyKCidVisible = true;
        this.CopyKCidform.mem_id = '';
      },
      //进入二级分类
      toClass(index, row) {
        this.$router.push({
          path: 'Videolesson/Videolessonlist',
          query: {
            id: row.id,
		    mem_id:row.mem_id
          }
        });
      },
      //添加修改
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let url = 'v1/addjgCourseVideo';
            if (this.classid != null) {
              url = 'v1/setjgCourseVideo';
              this.ruleForm.course_id = this.classid
            };
            let that = this;
            let data = this.ruleForm;
            that.$post(url, data).then((response) => {
              console.log('成功', response);
              if (response.code == 1000) {
                that.$message({
                  type: 'success',
                  message: '成功!'
                });
                that.dialogVisible = false;
                // 清空数据
                that.ruleForm = {
                  name: '',
                  password:'',
                  valkm:'',
                  cover_img: [],
                  sort: ''
                };
                //过去列表
                that.ObjAjax();
              };
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      //搜索
      scIN() {
        this.form.page = 1;
        this.ObjAjax();
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.form.page = val;
        //机构列表渲染
        this.ObjAjax();
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.form.page = val;
        //机构列表渲染
        this.ObjAjax();
      },
      //编辑
      handleEdit(state, index, row) {
        console.log(index, row);
        //弹窗打开
        if (state == '2') {
          this.classid = row.id;
          // 分类赋值
          this.ruleForm= row;
        } else {
          // 清空数据
          this.ruleForm = {
            name: '',
            password:'',
            valkm:'',
            cover_img: [],
            sort: ''
          };
          this.classid = null;
        };
        this.dialogVisible = true;
      },
      //删除
      handleOK(index, row) {
        console.log(index, row);
        this.$confirm('此操作将删除（' + row.name + '）分类, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let that = this;
          let data = {
            course_id: row.id
          };
          that.$post('v1/deljgCourseVideo', data).then((response) => {
            console.log('成功', response);
            if (response.code == 1000) {
              that.$message({
                type: 'success',
                message: '删除成功!'
              });
              //机构列表渲染
              that.ObjAjax();
            };
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });
        });
      },
      handleClose(done) {
        // 关闭窗
        done();
      },
      //列表
      ObjAjax() {
        this.$storeState.Videolesson = this.form;
        let that = this;
        let data = this.form;
        that.$post('v1/getjgCourseVideo', data).then((response) => {
          console.log('成功', response);
          if (response.code == 1000) {
            this.tableData = response.data.info;
            this.num = response.data.num;
          };
          if (response.error_code == 10000) {
            that.tableData = [];
          };
        });
      },
      //课目分类接口
      objKm(){
      	let that =this;
      	that.$post('v1/getjggroupCategory').then((response) => {
      		if (response.code==1000) {
      			this.optionskm = response.data
      		};
      	});
      },
    },
    mounted() {
      this.ObjAjax();
      this.objKm();
    },
    created() {
      //验证选中的状态是否存才
      if (this.$storeState.Videolesson != null) {
        this.form = this.$storeState.Videolesson;
      };
    }
  }
</script>

<style scoped lang="less">
  @import '../../../assets/style/index.less'; //颜色样式

  .InBoxBody {
    width: 100%;
    border: @borderbox;
    background: @bgcolor;
    border: 1px solid @bordersolid;
    border-radius: 10px;
    overflow: hidden;

    .InBoxBody-header {
      width: calc(100% - 40px);
      padding: 20px 20px;

      .InBoxBody-header-text {
        padding-left: 6px;
        font-size: 16px;
        border-left: @borderliftsolid;
      }

      .buttonRight {
        float: right;
      }
    }

    .InBoxBody-con {
      width: 100%;
      overflow: hidden;

      .InBoxBody-con-input {
        width: 100%;
        overflow: hidden;
        margin-bottom: 20px;

        span {
          margin-left: 20px;
          color: @color4;
          font-size: 16px;
          float: left;
          line-height: 32px;
        }

        ;

        .input {
          float: left;
          width: 200px;
          height: 32px;
          margin-left: 12px;
        }

        ;

        .time {
          float: left;
          height: 32px;
          margin-left: 12px;
        }
      }

      ;

      .pagination {
        float: right;
        margin: 20px;
      }

      .logo {
        width: 50px;
        height: 50px;
        display: block;
        float: left;
        margin-top: 4px;
      }

      .logours {
        float: left;
        margin-left: 10px;
        width: 80px;
        text-align: left;

        div {
          line-height: 60px;
        }
      }
    }
  }

  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 120px;
    height: 120px;
    line-height: 120px;
    text-align: center;
    border: 1px dashed #d9d9d9;
  }

  .avatar {
    width: 120px;
    height: 120px;
    display: block;
  }

  .dialoginput {
    width: 100%;
    margin: 20px 0;
    overflow: hidden;

    .dialoginputFl {
      width: calc(50% - 10px);
      margin-right: 10px;
      float: left;
    }
  }
</style>



