<template>
  <div class="liftnav" :style="'width:'+navwidth+'px'">
    <div class="liftnavcon">
      <div class="liftnav-headerimg" v-show="isCollapse==false?true:false">
        <div class="liftnav-headerimg-box">
          <img :src="$storeState.logo" alt="">
        </div>
				<div style="font-size: 12px;color: #666666;text-align: center;">机构ID:{{userJGinof.mem_id}}</div>
        <div style="font-size: 14px;color: #666666;text-align: center;">{{userJGinof.cname}}</div>
      </div>
      <div class="liftnav-headerimgx" v-show="isCollapse==false?false:true">
        <img :src="$storeState.logo" alt="">
      </div>
      <div class="liftnavconlist">
        <el-menu router :default-active="$route.path" unique-opened class="el-menu-vertical-demo" @open="handleOpen"
          @close="handleClose" :collapse="isCollapse" :collapse-transition="navames">
          <!-- 导航 -->
          <!-- 无下拉 -->
          <el-menu-item index="/index">
            <i class="el-icon-house"></i>
            <span slot="title">首页</span>
          </el-menu-item>
          <!-- 数据统计 -->
          <el-submenu index="2">
            <template slot="title">
              <i class="el-icon-data-analysis"></i>
              <span slot="title">数据统计</span>
            </template>
            <el-menu-item index="/Dataanalysis">数据分析</el-menu-item>
            <el-menu-item index="/OrderAnalysis">订单分析</el-menu-item>
          </el-submenu>
          <!-- 教务管理 -->
          <el-submenu index="3">
            <template slot="title">
              <i class="el-icon-pie-chart"></i>
              <span slot="title">用户管理</span>
            </template>
            <el-menu-item index="/Student">用户信息</el-menu-item>
          </el-submenu>
          <!-- 订单管理 -->
          <el-submenu index="4">
            <template slot="title">
              <i class="el-icon-document-copy"></i>
              <span slot="title">订单管理</span>
            </template>
            <el-menu-item index="/orderUnpaid">已付款订单</el-menu-item>
            <el-menu-item index="/orderConduct">进行中订单</el-menu-item>
            <el-menu-item index="/orderlesson">体验课订单</el-menu-item>
            <el-menu-item index="/orderrefund">退款订单</el-menu-item>
            <el-menu-item index="/orderHistory">历史订单</el-menu-item>
          </el-submenu>
          <!-- 课程管理 -->
          <el-submenu index="5">
            <template slot="title">
              <i class="el-icon-date"></i>
              <span slot="title">课程管理</span>
            </template>
            <el-menu-item index="/ClassesAdmin">课目信息</el-menu-item>
            <el-menu-item index="/coursemanagement">课程信息</el-menu-item>
            <el-menu-item index="/Videolesson">视频课程</el-menu-item>
          </el-submenu>
          <!-- 机构管理 -->
          <el-submenu index="6">
            <template slot="title">
              <i class="el-icon-office-building"></i>
              <span slot="title">机构管理</span>
            </template>
            <el-menu-item index="/Classroom">教室管理</el-menu-item>
            <!-- <el-menu-item index="/TeacherAdmin">老师管理</el-menu-item> -->
          </el-submenu>
          <!-- 营销管理 -->
          <el-submenu index="7">
            <template slot="title">
              <i class="el-icon-c-scale-to-original"></i>
              <span slot="title">营销管理 <img src="../../assets/images/HOT.gif" alt=""></span>
            </template>
            <el-menu-item index="/Experience">体验课<img style="margin-left: 6px;" src="../../assets/images/HOT.gif" alt=""></el-menu-item>
            <el-menu-item index="/Secondkill">秒杀课<img style="margin-left: 6px;" src="../../assets/images/HOT.gif" alt=""></el-menu-item>
          </el-submenu>
          <!-- 成长中心管理 -->
          <!-- <el-submenu index="8">
						<template slot="title">
						  <i class="el-icon-c-scale-to-original"></i>
						  <span slot="title">成长中心</span>
						</template>
						<el-menu-item index="/Complex">成长中心申请</el-menu-item>
					</el-submenu> -->
          <!-- 财务管理 -->
          <el-submenu index="9">
            <template slot="title">
              <i class="el-icon-money"></i>
              <span slot="title">财务管理</span>
            </template>
            <el-menu-item index="/FinaAnal">财务分析</el-menu-item>
            <el-menu-item index="/BudgetData">收支明细</el-menu-item>
          </el-submenu>
          <el-submenu index="10">
            <template slot="title">
              <i class="el-icon-setting"></i>
              <span slot="title">系统管理</span>
            </template>
            <el-menu-item index="/IntroCon">机构介绍</el-menu-item>
            <el-menu-item index="/Instinfo">机构资质</el-menu-item>
            <!-- <el-menu-item index="/PrivAdmin">权限管理</el-menu-item> -->
            <el-menu-item index="/SetUp">系统设置</el-menu-item>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <!-- 收缩块 -->
    <div class="liftnav-button" @click="navHs()"></div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        userJGinof: [],
        isCollapse: false,
        navames: false,
        navwidth: 200,
      };
    },
    methods: {
      handleOpen(key, keyPath) {
        // console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        // console.log(key, keyPath);
      },
      //导航回收展开
      navHs() {
        if (this.isCollapse === false) {
          this.isCollapse = true;
          this.navwidth = 64;
          this.$emit('ConHei', 'calc(100% - 64px)');
        } else {
          this.isCollapse = false;
          this.navwidth = 200;
          this.$emit('ConHei', 'calc(100% - 200px)');
        };
      },
      // 用户信息
      userinfo() {
        let data = {};
        this.$post('v1/getMemberinformation', data).then((response) => {
          if (response.code == 1000) {
            this.userJGinof = response.data;
            this.$storeState.logo = response.data.logo[0];
			      this.$storeState.useridnam = response.data.mem_id;
            this.$storeState.JGgllist = response.data.binding_member;
          };
        })
      }
    },
    mounted() {
      // console.log(this.$route.path)
      this.userinfo();
    },
  }
</script>

<style scoped lang="less">
  .el-menu {
    border: none;
    background-color: inherit;
  }

  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }

  .el-menu-item.is-active {
    color: @color2;
    background-color: @nackcolor;
  }

  @import '../../assets/style/index.less'; //颜色样式

  .liftnav {
    height: 100%;
    float: left;
    background-color: @navbackcolor;
    box-shadow: @navboxShadow;
    position: relative;

    .liftnavcon {
      width: 200px;
      height: 100%;
      overflow: hidden;

      .liftnavconlist {
        width: 200px;
        height: calc(100% - 120px);
        overflow-x: hidden;
        overflow-y: auto;
        padding-right: 15px;
      }
    }

    .liftnav-button {
      position: absolute;
      width: 14px;
      height: 80px;
      background-color: @color2;
      top: 50%;
      right: -14px;
      margin-top: -40px;
      border-top-right-radius: 80px;
      border-bottom-right-radius: 80px;
    }

    .liftnav-headerimg {
      width: 100%;
      height: 140px;

      .liftnav-headerimg-box {
        width: 80px;
        height: 80px;
        background-color: @color3;
        border-radius: 50%;
        margin: 20px auto;
      }

      img {
        display: block;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        overflow: hidden;
        float: left;
        margin: 10px;
      }
    }

    .liftnav-headerimgx {
      width: 64px;
      height: 64px;

      img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        overflow: hidden;
        display: block;
        float: left;
        margin: 12px;
      }
    }
  }

  .liftnavlogo {
    background-color: @color1;
    widows: 100%;
    height: 70px;

    img {
      width: 20%;
      display: block;
      margin: 0 auto;
    }
  }
</style>



