<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/Videolesson' }">视频课程</el-breadcrumb-item>
				<el-breadcrumb-item>视频课程列表</el-breadcrumb-item>
			</el-breadcrumb>
			<el-button class="buttonRight" round @click="handleEdit('1')">新 增</el-button>
		</div>
		<div class="InBoxBody-con">
			<div class="InBoxBody-con-input">
				<span>信息搜索</span>
				<el-form ref="form" :model="form" label-width="80px">
					<el-input class="input" v-model="form.name" placeholder="课程名称"></el-input>
					<el-button class="time" type="warning" plain @click="Search">搜 索</el-button>
				</el-form>
			</div>
			<!-- 表单 -->
			<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" stripe :header-cell-style="{background:'#eef1f6',color:'#606266'}">
				<el-table-column prop="cname" label="机构名称" align="center" fixed></el-table-column>
				<el-table-column prop="name" label="视频课程名" align="center" fixed></el-table-column>
				<el-table-column prop="sort" label="排序" align="center"></el-table-column>
				<el-table-column label="操作" width="360" align="center" fixed="right">
					<template slot-scope="scope">
						<el-button size="mini" type="warning" @click="handleEdit('2',scope.$index, scope.row)">编辑</el-button>
						<el-button size="mini" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
					</template>
				</el-table-column>
			</el-table>
			<div class="block pagination">
				<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="form.page"
				 :page-size="16" layout="prev, pager, next, jumper" :total="num">
				</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				value: '',
				//搜索信息
				form: {
					name: '',
					page: 1
				},
				// 总数
				num: 0,
				tableData: [],
			}
		},
		methods: {
			// 编辑
			handleEdit(state, index, row) {
				if (state != '2') {
					this.$router.push({
						path: '/Videolesson/VideolessonCon',
						query: {
							courseVideoid:this.$route.query.id,
							mem_id:this.$route.query.mem_id,
						}
					});
				} else {
					this.$router.push({
						path: '/Videolesson/VideolessonCon',
						query: {
							courseVideoid:this.$route.query.id,
							mem_id:this.$route.query.mem_id,
							mid: row.id
						}
					});
				}
			},
			// 搜索
			Search() {
				this.form.page = 1;
				this.XSinfo();
			},
			handleSizeChange(val) {
				this.form.page = val;
				this.XSinfo();
			},
			handleCurrentChange(val) {
				this.form.page = val;
				this.XSinfo();
			},
			//删除
			handleDelete(index, row) {
				this.$confirm('此操作将同意删除' + row.name + ', 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					let that = this;
					let data = {
						video_id: row.id
					};
					that.$post('v1/deljgVideo', data).then((response) => {
						if (response.code == 1000) {
							this.XSinfo();
						};
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消'
					});
				});
			},
			// 数据信息
			XSinfo() {
				let that = this;
				let data = this.form;
				data.course_video_id=this.$route.query.id;
				that.$post('v1/getjgVideo', data).then((response) => {
					if (response.code == 1000) {
						that.tableData = response.data.info;
						that.num = response.data.num;
					};
					if (response.error_code == 10000) {
						that.tableData = []
					};
				});
			},
			//课程分类接口
		},
		mounted() {
			this.XSinfo();
		}
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式

	.InBoxBody {
		width: 100%;
		border: @borderbox;
		background: @bgcolor;
		border: 1px solid @bordersolid;
		border-radius: 10px;
		overflow: hidden;

		.InBoxBody-header {
			width: calc(100% - 40px);
			padding: 20px 20px;

			.InBoxBody-header-text {
				padding-left: 6px;
				font-size: 16px;
				border-left: @borderliftsolid;
			}

			.buttonRight {
				float: right;
			}
		}

		.InBoxBody-con {
			width: 100%;
			overflow: hidden;

			.InBoxBody-con-input {
				width: 100%;
				overflow: hidden;
				margin-bottom: 20px;

				span {
					margin-left: 20px;
					color: @color4;
					font-size: 16px;
					float: left;
					line-height: 32px;
				}

				;

				.input {
					float: left;
					width: 200px;
					height: 32px;
					margin-left: 12px;
					margin-bottom: 12px;
				}

				;

				.time {
					float: left;
					height: 32px;
					margin-left: 12px;
					margin-bottom: 12px;
				}

			}

			;

			.pagination {
				float: right;
				margin: 20px;
			}

			.logo {
				width: 50px;
				height: 50px;
				display: block;
				margin: 0 auto;
			}
		}
	}
</style>



