import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'//登录
import Regist from '@/components/Regist'//注册
import index from '@/components/index'
import home from '@/components/AHome/home/home' //首页
//资质认证
import InstinfoDL from '@/components/Instinfo' //机构信息
//数据统计
import Dataanalysis from '@/components/BDataStat/Dataanalysis/Dataanalysis' //数据分析
import OrderAnalysis from '@/components/BDataStat/OrderAnalysis/OrderAnalysis' //订单分析
// 教务管理
import Student from '@/components/CEducAdmin/Student/Student' //学生信息
import Studdetails from '@/components/CEducAdmin/Student/Studdetails' //学生详情
import StuddetailsClass from '@/components/CEducAdmin/Student/StuddetailsClass' //用户信息详情上课信息
import evaluate from '@/components/CEducAdmin/evaluate/evaluate' //用户评价
//订单管理
import orderUnpaid from '@/components/DOrderAdmin/orderUnpaid/orderUnpaid' //已付款订单
import orderUnpaiddetails from '@/components/DOrderAdmin/orderUnpaid/orderUnpaiddetails' //已付款订单
import orderConduct from '@/components/DOrderAdmin/orderConduct/orderConduct' //进行中订单
import orderConductdetails from '@/components/DOrderAdmin/orderConduct/orderConductdetails' //进行中订单
import orderrefund from '@/components/DOrderAdmin/orderrefund/orderrefund' //退款订单
import orderrefunddetails from '@/components/DOrderAdmin/orderrefund/orderrefunddetails' //退款订单
import orderHistory from '@/components/DOrderAdmin/orderHistory/orderHistory' //历史订单
import orderHistorydetails from '@/components/DOrderAdmin/orderHistory/orderHistorydetails' //历史订单
import orderlesson from '@/components/DOrderAdmin/orderlesson/orderlesson' //体验课订单
import orderlessondetails from '@/components/DOrderAdmin/orderlesson/orderlessondetails' //体验课订单
// 课程管理
import ClassesAdmin from '@/components/ECourseAdmin/ClassesAdmin/ClassesAdmin' //课种管理
import ClassesAdminCon from '@/components/ECourseAdmin/ClassesAdmin/ClassesAdminCon' //课种管理详情
import coursemanagement from '@/components/ECourseAdmin/coursemanagement/coursemanagement' //课程管理
import CourGemCon from '@/components/ECourseAdmin/coursemanagement/CourGemCon' //课程管理编辑
import CourGeD from '@/components/ECourseAdmin/coursemanagement/CourGeD' //单个日期修改
import Videolesson from '@/components/ECourseAdmin/Videolesson/Videolesson' //视频课程
import Videolessonlist from '@/components/ECourseAdmin/Videolesson/Videolessonlist' //视频课程列表
import VideolessonCon from '@/components/ECourseAdmin/Videolesson/VideolessonCon' //视频课程编辑
//机构管理
import Classroom from '@/components/FOrgaAdmin/Classroom/Classroom' //教室管理
import ClassroomCon from '@/components/FOrgaAdmin/Classroom/ClassroomCon' //教室管理详情
import TeacherAdmin from '@/components/FOrgaAdmin/TeacherAdmin/TeacherAdmin' //教师管理
import TeacherAdminCon from '@/components/FOrgaAdmin/TeacherAdmin/TeacherAdminCon' //教师管理详情
//营销管理
import Experience from '@/components/GMarketing/Experience/Experience' //体验课管理
import ExperienceCon from '@/components/GMarketing/Experience/ExperienceCon' //体验课管理详情
import ExperienceD from '@/components/GMarketing/Experience/ExperienceD' //体验课管理单个
import Secondkill from '@/components/GMarketing/Secondkill/Secondkill' //秒杀管理
import SecondkillCon from '@/components/GMarketing/Secondkill/SecondkillCon' //秒杀管理
import SecondkillD from '@/components/GMarketing/Secondkill/SecondkillD' //秒杀管理
//综合体
import Complex from '@/components/HComplex/Complex/Complex' //综合体
import ComplexCon from '@/components/HComplex/Complex/ComplexCon' //综合体
// 财务管理
import FinaAnal from '@/components/YFinaAdmin/FinaAnal/FinaAnal' //财务分析
import BudgetData from '@/components/YFinaAdmin/BudgetData/BudgetData' //收支明细
// 系统设置
import Instinfo from '@/components/ZSystAdmin/Instinfo/Instinfo' //机构信息
import IntroCon from '@/components/ZSystAdmin/Introduction/IntroCon' //机构介绍内容
import Introduction from '@/components/ZSystAdmin/Introduction/Introduction' //机构介绍
import PrivAdmin from '@/components/ZSystAdmin/PrivAdmin/PrivAdmin' //权限管理
import SetUp from '@/components/ZSystAdmin/SetUp/SetUp' //系统设置

const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}
Vue.use(Router)

export default new Router({
	routes: [{
			path: '*', //默认找不到路由时  重定向页面
			redirect: '/Login'
		},{
			path: '/Login',
			name: '登录',
			component: Login,
		},{
			path: '/Regist',
			name: '注册',
			component: Regist,
		},
		{
			path: '/InstinfoDL',
			name: '资质认证',
			component: InstinfoDL,
		},{
		path: '/index',
		name: 'index',
		component: index,
		children: [{
				path: '/index',
				name: '18',
				component: home
			},
			//数据统计
			{path: '/Dataanalysis',name: '19',component: Dataanalysis},
			{path: '/OrderAnalysis',name: '20',component: OrderAnalysis},
			// 教务管理
			{path: '/Student',name: '21',component: Student},
			{path: '/Student/Studdetails',name: '21',component: Studdetails},
			{path: '/Student/StuddetailsClass',name: '21',component: StuddetailsClass},
			{path: '/evaluate',name: '21',component: evaluate},
			//订单管理
			{path: '/orderUnpaid',name: '22',component: orderUnpaid},
			{path: '/orderUnpaid/orderUnpaiddetails',name: '22',component: orderUnpaiddetails},
			{path: '/orderConduct',name: '23',component: orderConduct},
			{path: '/orderConduct/orderConductdetails',name: '23',component: orderConductdetails},
			{path: '/orderlesson',name: '24',component: orderlesson},
			{path: '/orderlesson/orderlessondetails',name: '24',component: orderlessondetails},
			{path: '/orderrefund',name: '25',component: orderrefund},
			{path: '/orderrefund/orderrefunddetails',name: '25',component: orderrefunddetails},
			{path: '/orderHistory',name: '26',component: orderHistory},
			{path: '/orderHistory/orderHistorydetails',name: '26',component: orderHistorydetails},
			// 课程管理
			{path: '/ClassesAdmin',name: '27',component: ClassesAdmin},
			{path: '/ClassesAdmin/ClassesAdminCon',name: '27',component: ClassesAdminCon},
			{path: '/coursemanagement',name: '28',component: coursemanagement},
			{path: '/coursemanagement/CourGemCon',name: '28',component: CourGemCon},
			{path: '/coursemanagement/CourGeD',name: '28',component: CourGeD},
      {path: '/Videolesson',name: '28',component: Videolesson},
      {path: '/Videolesson/VideolessonCon',name: '28',component: VideolessonCon},
      {path: '/Videolesson/Videolessonlist',name: '28',component: Videolessonlist},
			//机构管理
			{path: '/Classroom',name: '29',component: Classroom},
			{path: '/Classroom/ClassroomCon',name: '29',component: ClassroomCon},
			{path: '/TeacherAdmin',name: '29',component: TeacherAdmin},
			{path: '/TeacherAdmin/TeacherAdminCon',name: '29',component: TeacherAdminCon},
			// 营销管理
			{path: '/Experience',name: '30',component: Experience},
			{path: '/Experience/ExperienceCon',name: '30',component: ExperienceCon},
			{path: '/Experience/ExperienceD',name: '30',component: ExperienceD},
			{path: '/Secondkill',name: '31',component: Secondkill},
			{path: '/Secondkill/SecondkillCon',name: '31',component: SecondkillCon},
			{path: '/Secondkill/SecondkillD',name: '31',component: SecondkillD},
			//综合体
			{path: '/Complex',name: 'Complex',component: Complex},
			{path: '/Complex/ComplexCon',name: 'ComplexCon',component: ComplexCon},
			// 财务管理
			{path: '/FinaAnal',name: '32',component: FinaAnal},
			{path: '/BudgetData',name: '37',component: BudgetData},
			// 系统设置
			{path: '/IntroCon',name: '33',component: IntroCon},
			{path: '/IntroCon/Introduction',name: '33',component: Introduction},
			{path: '/Instinfo',name: '34',component: Instinfo},
			{path: '/PrivAdmin',name: '9',component: PrivAdmin},
			{path: '/SetUp',name: '35',component: SetUp},
		]
	}]
})



