<template>
	<div class="InBoxBody">
		<div class="InBoxBody-header">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/Videolesson' }">视频课程信息</el-breadcrumb-item>
				<el-breadcrumb-item :to="{ path: '/Videolesson/Videolessonlist?id='+$route.query.courseVideoid+'&&mem_id='+$route.query.mem_id }">视频课程列表</el-breadcrumb-item>
				<el-breadcrumb-item>视频课程信息编辑</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="InBoxBody-con">
			<el-divider content-position="left">视频课程信息</el-divider>
			<div class="InBoxBody-con-input">
				<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
					<div class="InBoxBody-con-input-style">
						<el-form-item label="课程标题" prop="name">
							<el-input size="large" class="flattextarea" placeholder="请输入标题" v-model="ruleForm.name"></el-input>
						</el-form-item>
						<el-form-item label="排序" prop="sort">
							<el-input size="large" class="flattextarea" placeholder="请输入排序" v-model="ruleForm.sort"></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="课程简介:" prop="introduction ">
							<el-input class="flattextarea" type="textarea" :rows="10" placeholder="请输入内容" v-model="ruleForm.introduction"></el-input>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style">
						<el-form-item label="封面单图:" prop="cover_img">
							<el-upload action="https://zht.insooner.com/jg/v1/getUpdatePictures" list-type="picture-card" :file-list="ruleForm.cover_img"
							 :on-preview="handlePictureCardPreview" :on-success="handsuccess" :on-remove="handleRemove" :limit="1">
								<i class="el-icon-plus"></i>
							</el-upload>
							<el-dialog :visible.sync="dialogVisible">
								<img width="100%" :src="dialogImageUrl" alt="">
							</el-dialog>
						</el-form-item>
						<div slot="tip" class="el-upload__tip">上传jpg/png文件，尺寸400*400,大小不超过2M</div>
					</div>
					<!-- 视频上传 -->
					<div class="InBoxBody-con-input-style">
						<el-form-item label="视频上传:" prop="video">
							<el-upload
							  class="upload-demo"
							  action=''
							  :before-upload="beforeUploadVideo"
							  :on-remove="handleVODRemove"
							  :http-request="hadhttprequest"
							  multiple
							  :limit="1"
							  :file-list="ruleForm.video">
							  <el-button v-if="butstate==0" size="small" type="primary">点击上传 <i class="el-icon-upload el-icon--right"></i></el-button>
							  <el-button v-else-if="butstate==1" slot="tip" type="info" :loading="true" disabled>视频正在向服务器上传请稍等...</el-button>
							  <el-button v-else-if="butstate==2" slot="tip" type="success"  disabled>已存在视频，请删除原先视频在进行上传！</el-button>
							  <div slot="tip" class="el-upload__tip">请保证视频格式正确，且不超过1G</div>
							</el-upload>
						</el-form-item>
					</div>
					<div class="InBoxBody-con-input-style" style="margin-top: 20px;">
						<el-button v-if="butstate!=1" style="margin-left:90px;width:80px;" type="warning" @click="submitForm('ruleForm')">确 认</el-button>
						<el-button v-else-if="butstate==1" style="margin-left:90px;width:160px;" type="warning" disabled>视频上传中请耐心等待</el-button>
						<el-button style="width:80px;" type="warning" plain @click="$router.go(-1)">取 消</el-button>
					</div>
				</el-form>
			</div>
		</div>
	</div>
</template>

<script>
   import OSS from 'ali-oss';
	export default {
		data() {
			return {
				// 视频文件上传路由
				uploadUrl: 'https://zht.insooner.com/jg/v1/getUpdateVideo',
				ruleForm: {
					name: '',
					sort:'',
					introduction:'',
					cover_img: [],
					video:[],
				},
				rules: {
					name: [{
						required: true,
						message: '请填写标题',
						trigger: 'blur'
					}],
					sort: [{
						required: true,
						message: '请填写排序',
						trigger: 'blur'
					}],
					introduction: [{
						required: true,
						message: '请填写说明',
						trigger: 'blur'
					}],
					cover_img: [{
						required: true,
						message: '请上传图片',
						trigger: 'blur'
					}],
				},
				//图片
				dialogImageUrl: '',
				dialogVisible: false,
				// 视频上传按钮变化
				butstate:0
			}
		},
		methods: {
			// 视频上传
			beforeUploadVideo(file) {
				const isLt1G = file.size / 1024 / 1024 < 1024;
				if (['video/mp4', 'video/ogg', 'video/flv', 'video/avi', 'video/wmv', 'video/rmvb'].indexOf(file.type) == -1) {
					this.$message.error('请正确上传视频格式');
					return false;
				};
				if (!isLt1G) {
					this.$message.error('不能超过1G哦!');
					return false;
				};
				this.butstate=1;
			},
			handleVODRemove(file, fileList) {
				// console.log(file, fileList);
				this.ruleForm.video = fileList;
				this.butstate=0;
			},
			//接口请求
		    async getOssToken(data,content){
				let that = this;
				// 视频文件名称
				let videoName = 'productvideo/'+that.$storeState.useridnam+'/'+Date.parse(new Date())/1000+content.file.name;
				let client = new OSS({
					 region: 'oss-cn-hangzhou',
					 accessKeyId: data.AccessKeyId,
					 accessKeySecret: data.AccessKeySecret,
					 stsToken: data.SecurityToken,
					 bucket:'yinxuejiaoyu'
				 });
				 try {
					let result = await client.multipartUpload(videoName , content.file, {
						progress: async function (p) {//这是上传进度条
							content.onProgress({percent: parseInt(p * 100)+'%', returnValue: true });
							if (p==1) {
								that.butstate=2;
							};
						}
					 });
					return (that.ruleForm.video[0]  = [result].map(curr =>{//on-change回调的参数
						return {
							name:Date.parse(new Date())/1000+content.file.name,
							url: curr.res.requestUrls[0].split('?')[0],
							response:curr.res.requestUrls[0].split('?')[0],
						}
					})[0]);
				} catch (e) {
					 console.log('失败',e)
					if (e.code === 'ConnectionTimeoutError') {
						content.onError('视频上传超时')
						throw "视频上传超时!";
					}else{
						content.onError('视频上传失败')
					}
				}
			},
			// 视频自定义上传
			async hadhttprequest(content){
				let that=this;
				this.$axios.get('https://zht.insooner.com/ststoken.php').then(function(res){
					that.getOssToken(res.data.Credentials,content);
				}).catch(function (error) {
					console.log(error);
				});
			},
			
			//图片上传
			handleRemove(file, fileList) {
				this.ruleForm.cover_img = fileList;
			},
			handlePictureCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			// 上传图片成功
			handsuccess(file, fileList) {
				console.log(file, fileList);	
				this.ruleForm.cover_img.push(fileList);
			},
			//提交
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let that = this;
						let data = this.ruleForm;
						let tjurl = 'v1/addjgVideo';
						if (that.$route.query.mid != undefined) {
							tjurl = 'v1/setjgVideo';
							data.video_id = that.$route.query.mid;
						}else{
							data.course_video_id = that.$route.query.courseVideoid;
							data.mem_id = that.$route.query.mem_id;
						}
						that.$post(tjurl, data).then((response) => {
							if (response.code == 1000) {
								that.$message({
									message: '提交成功',
									type: 'success'
								});
								that.$router.go(-1);
							};
						});
					} else {
						return false;
					}
				});
			},
			// 关闭弹出
			handleClose(done) {
				done();
			},
			// 获取详情
			ObjectCon() {
				let that = this;
				let data = {
					video_id: this.$route.query.mid,
				};
				that.$post('v1/getjgVideodetails', data).then((response) => {
					if (response.code == 1000) {
						that.ruleForm = response.data;
						if (that.ruleForm.video.length!=0) {
							that.butstate =2
						};
					};
				});
			}
		},
		mounted() {
			// 获取详情
			//是否存在id
			if (this.$route.query.mid != undefined) {
				this.ObjectCon();
			};
		},
	}
</script>

<style scoped lang="less">
	@import '../../../assets/style/index.less'; //颜色样式
	@import './VideolessonCOM/VideolessonCon.less'; //颜色样式
</style>
<style>
	.is-selected {
		color: #1989FA;
	}
</style>



